import React from 'react'
import Build from '../../images/program.svg'

export default function Building() {
    return (
        <div>
            <div className="m-12">
                <div className="container mx-auto py-8 px-8 md:px-24 xl:px-48 mb-20 sm:mb-32 flex justify-between bg-indigo-500">
                    <div className="text-2xl font-bold flex justify-center items-center text-white">
                        This section of the page is still being built.
                    </div>
                    <div>
                        <img className="w-80 h-auto" src={Build} />
                    </div>
                </div>
            </div>
        </div>
    )
}
