import React from 'react'
import Items from './cards/Items'
import mlProject from '../images/projects-img/ml-front.png'
import WeatherImg from '../images/projects-img/weather.jpg'
import BlogTemplate from '../images/projects-img/blog-template.jpg'
import Sunny from '../images/projects-img/sunny.jpg'
import tipApp from '../images/projects-img/tip-app.jpg'
import GitApp from '../images/projects-img/gitapp.png'
import { faBootstrap, faCss3, faHtml5, faJs, faReact, faSass } from '@fortawesome/free-brands-svg-icons'

export default function ItemsMain() {

    return (
        <div className="container mx-auto px-8 md:px-10 xl:px-20 mt-20">
            <div className="grid md:grid-cols-3 gap-5 xl:gap-7">

                <Items 
                    image={GitApp}
                    name="Github user finder"
                    
                    //Stacks
                    html={faHtml5}
                    sass={faSass}
                    js={faJs}

                    link="Visit"
                    source="Source Code"
                />

                <Items 
                    image={mlProject}
                    name="Image Reco App"
                    
                    //Stacks
                    html={faHtml5}
                    css={faCss3}
                    react={faReact}

                    link="Visit"
                    source="Source Code"
                />

                <Items 
                    image={tipApp}
                    name="Tip Calculation App"

                    //Stacks
                    html={faHtml5}
                    sass={faSass}
                    js={faJs}

                    link="Visit"
                    source="Source Code"
                />

                <Items 
                    image={WeatherImg}
                    name="Weather App"

                    //Stacks
                    html={faHtml5}
                    bootstrap={faBootstrap}
                    js={faJs}

                    link="Visit"
                    source="Source Code"
                />
                <Items 
                    image={BlogTemplate}
                    name="Blog HTML Template"

                    //Stacks
                    html={faHtml5}
                    css={faCss3}
                    bootstrap={faBootstrap}

                    link="Visit"
                    source="Source Code"
                />
                <Items 
                    image={Sunny}
                    name="Sunny Landing Page"

                    //Stacks
                    html={faHtml5}
                    sass={faSass}
                    js={faJs}

                    link="Visit"
                    source="Source Code"
                />
            </div>
        </div>
    )
}
